export default {
	data() {
		return {
			detailList: [{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				}
			],
			money: 0,
			orderNum: 0,
			peopleNum: 0,
			userName: '',
			mobile: '',
			remarks: '',
			orderId: '',
			makeTime: '',
			orderDetails: [{
					name: '订单号',
					value: '1321 1478 1478 4874 788'
				},
				{
					name: '用餐人数',
					value: '便于商家携带',
				},
				{
					name: '联系人',
					value: '张女士 13656564898',
				},
				{
					name: '订单备注',
					value: '多放辣椒少放盐多放辣椒少放盐多放辣椒少放盐多放辣椒少放盐',
				}
			]
		}
	},
	onLoad(e) {
		this.orderId = e.id;
		// console.log()
		this.getOrderDetail(e.id);
	},
	methods: {
		confirmMeal() { // 确认取餐
			///foods/shopcar/accept
			let that = this
			uni.request({
				url: this.nowUrl + '/foods/shopcar/accept?id='+this.orderId,
				method: 'POST',
				header: {
					"content-type": "application/json",
					"token": this.token
				},
				success: (res) => {
					console.log(that.orderId)
					if(res.data.code == 200){
						uni.navigateTo({
							url: '../confirmMeal/confirmMeal?id='+ that.orderId,
							animationType: 'slide-in-right',
							animationDuration: 200
						});
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration:1000
						})
					}
					
					
				}
			})

		},
		//获取订单详情
		getOrderDetail(id) {
			uni.request({
				url: this.nowUrl + '/foods/myOrder/find?id=' + id,
				method: 'GET',
				header: {
					"content-type": "application/json",
					"token": this.token
				},
				success: (res) => {
					// console.log(res.data.data);
					this.detailList = res.data.data.foods;
					this.money = res.data.data.priceAll;
					this.peopleNum = res.data.data.peopleNum;
					this.userName = res.data.data.userName;
					this.mobile = res.data.data.mobile;
					this.orderNum = res.data.data.orderNum;
					this.makeTime = res.data.data.makeTime;
					if (res.data.data.remarks) {
						this.remarks = res.data.data.remarks;
					}

				}
			});
		}
	}
}
